<template>
    <div class="home-page">
        <img src="../../assets/logo.png" alt="">
        <h4>实践小题目</h4>
        <button class="check-demo" @click="toDemo">查看Demo</button>
        <button class="download-app" @click="toWebsite">逛逛官网</button>

    </div>
</template>

<script>
import PageHeader from '../../components/page-header.vue'

export default {
    data() {
        return {
            name: 'page'
        }
    },
    components: {
        PageHeader
    },
    methods: {
        toDemo () {
            this.$router.push('list');
        },
        toWebsite () {
            window.open('https://www.ihomefnt.com/');
        }
    }
}
</script>

<style lang='less'>
.home-page {
    position: absolute;
    width: 100%;
    z-index: 20;
    text-align: center;
    height: 100%;
    background-color: rgb(151, 246, 185);
    background-image: url(../../assets/yizi.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 60% 50%;
    img {
        margin-top: 0.5rem;
    }
    h4 {
        margin-top: 0.2rem;
        font-size: 0.2rem;
        color: #fff;
    }
    .check-demo {
        background: #fff;
        border: none;
        display: block;
        margin: 75% auto 0;
        width: 70%;
        height: 0.4rem;
    }
    .download-app {
        border: 1px solid #fff;
        background: rgba(0, 0, 0, 0);
        margin-top: 0.15rem;
        width: 70%;
        height: 0.4rem;
    }
}
</style>



