<template>
  <div class="page-header">
      <i @click="back">
          <img src="../assets/icon-font/return.svg" alt="">
      </i>
      <div class="header-title">套装列表</div>
      <span></span>
  </div>
</template>

<script>
    export default {
        methods: {
            back () {
                this.$router.go(-1);
            }
        }
    }
</script>

<style lang='less'>
    .page-header {
        flex-shrink: 0;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        height: 0.5rem;
        border-bottom: 1px solid #ddd;
        background: #fff;
        color: #000;
        .header-title {
            width: 70%;
            text-align: center;
            font-size: 0.16rem;
        }
        i, span {
            width: 15%;
            height: 0.3rem;
            text-align: center
        }
        img {
            width: 0.2rem;
            height: 0.3rem;
        }
    }
</style>




