<template>
  <div class="page-tabbar">
      <ul class="type-ul" @touchmove.prevent>
          <li @click="chooseTab('BUILDING')" :class="{'chosen': chosenItem=='BUILDING'}">楼盘 <img src="../assets/icon-font/up_arrow.svg" alt=""></li>
          <li @click="chooseTab('STYLE')" :class="{'chosen': chosenItem=='STYLE'}">风格 <img src="../assets/icon-font/up_arrow.svg" alt=""></li>
      </ul>
      <transition name="cShow">
        <ul v-show='chosenItem !== "NONE"' class="styles-ul">
            <li @touchstart="click($event, item)" v-for="item in listData">{{item}}</li>
        </ul>
      </transition>
      <transition name="coShow">
        <div @touchmove="noScroll($event)" @touchstart="noScroll($event)" v-show='chosenItem !== "NONE"' class='cover'></div>
      </transition>
  </div>
</template>

<script>
    export default {
        data () {
            return {
                chosenItem: 'NONE',
                listData: [1, 2, 3, 4, 5]
            }
        },
        methods: {
            click (e, item) {
                e.stopPropagation();
                e.preventDefault();
                console.log(item);
            },
            chooseTab (tabName) {
                if (this.chosenItem === tabName && this.chosenItem !== 'NONE') {
                    this.chosenItem = 'NONE';
                } else {
                    this.chosenItem = tabName;
                    if (tabName === 'BUILDING') {
                        this.listData = this.typeData.building;
                    } else {
                        this.listData = this.typeData.style;
                    }
                }
            },
            noScroll (e) {
                e.preventDefault();
                e.stopPropagation();
                this.chosenItem = 'NONE';
            }
        },
        props: {
            typeData: {
                type: Object,
                default () {
                    return {};
                }
            }
        }
    }
</script>

<style lang='less'>
.page-tabbar {
    flex-shrink: 0;
    position: absolute;
    top: 0.5rem;
    width: 100%;
    z-index: 10;
    background: #fff;
    .type-ul {
        display: flex;
        justify-content: space-between;
        li {
            width: 50%;
            text-align: center;
            line-height: 0.4rem;
            border-bottom: 2px solid #eee;
            img {
                transition: transform 0.4s;
                width: 0.12rem;
                height: 0.12rem;
            }
        }
        li.chosen {
            color: orange;
            img {
                transform: rotate(180deg);
                color: orange;
            }
            
        }
    }
    .styles-ul {
        transform-origin: 0 0;
        background: #fff;
        position: absolute;
        z-index: 10;
        width: 100%;
        top: 0.42rem;
        li {
            padding: 0 0.2rem;
            line-height: 0.4rem;
            border-bottom: 1px solid #eee;
        }
    }
    .cover {
        position: absolute;
        z-index: 9;
        width: 100%;
        top: 0.42rem;
        height: 10rem;
        background-color: #000;
        opacity: 0.5;
        overflow: hidden;
    }
    // 动画
    .cShow-enter-active,
    .cShow-leave-active {
        transition: all 0.4s;
    }

    .cShow-enter,
    .cShow-leave-to {
        opacity: 0;
        transform: scale(1, 0);
    }

    .coShow-enter-to,
    .coShow-leave {
        opacity: 0.5
    }
    .coShow-enter,
    .coShow-leave-to {
        opacity: 0;
    }
}
</style>




