import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import HomePage from '@/pages/home-page'
import ListPage from '@/pages/list-page'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: HelloWorld
    },
    {
      path: '/home',
      name: 'Home',
      component: HomePage
    },
    {
      path: '/list',
      name: 'List',
      component: ListPage
    }
  ]
})



