<template>
    <transition name="loading-pop-show">
        <section class="loading-pop-box">
            <section class="loading-pop-content">
                <section class="loading-box loading-box-min">
                    <svg class="loading-spinner" width="100%" height="100%" viewBox="0 0 66 66" xmlns="http://www.w3.org/2000/svg">
                    <circle class="loading-path" fill="none" stroke-width="6" stroke-linecap="round" cx="33" cy="33" r="30"></circle>
                    </svg>
                </section>
                <slot></slot>
            </section>
        </section>
    </transition>
</template>

<script>

export default {
    
};
</script>

<style lang='less'>
@import url('../config/global-style/config/config.less');
.loading-pop-box {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading-pop-content {
  background: #fff;
  padding: 0.1rem;
  border-radius: 0.1rem;
  display: flex;
  align-items: center;
}


.loading-pop-show-enter-active,
.loading-pop-show-leave-active {
  transition: opacity 0.2s;
}

.loading-pop-show-enter,
.loading-pop-show-leave-to {
  opacity: 0.1;
}

.loading-pop-show-enter-to,
.loading-pop-show-leave {
  opacity: 1;
}




.loading-box {
    height: 100%;
    padding: @size-space-second;
}

.loading-box-min {
    width: 0.4rem;
    height: 0.4rem;
}

@offset: 187;
@duration: 1.4s;

.loading-spinner {
    animation: rotator @duration linear infinite;
}

@keyframes rotator {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(270deg);
    }
}

.loading-path {
    stroke-dasharray: @offset;
    stroke-dashoffset: 0;
    transform-origin: center;
    stroke: @color-assist-first;
    animation: loading-dash @duration ease-in-out infinite; //,loading-colors (@duration*4) ease-in-out infinite;
}

//@keyframes loading-colors {
//  0% { stroke: #4285F4; }
//  25% { stroke: #DE3E35; }
//  50% { stroke: #F7C223; }
//  75% { stroke: #1B9A59; }
//  100% { stroke: #4285F4; }
//}
@keyframes loading-dash {
    0% {
        stroke-dashoffset: @offset;
    }
    50% {
        stroke-dashoffset: @offset/4;
        transform: rotate(135deg);
    }
    100% {
        stroke-dashoffset: @offset;
        transform: rotate(450deg);
    }
}

</style>



