<template>
    <div class="list-page">
        <page-header></page-header>
        <page-tabbar :typeData="typeData"></page-tabbar>
        <ul ref='scrollUl' class='item-ul' @scroll="scrollUl">
            <page-item :info="item" v-for="item in list"></page-item>
            <li class="prompt" v-show="isPromptShow">没有更多数据啦~</li>
            <li class="prompt" v-show="isLoadingLiShow">数据加载中。。。</li>
        </ul>
        <loading v-show="isLoadingShow" >加载中...</loading>
    </div>
</template>

<style lang='less'>
.list-page {
    height: 100%;
    display: flex;
    flex-direction: column;
    .item-ul {
        margin-top: 0.4rem;
        flex: 1;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
    }
    .prompt {
        line-height: 0.4rem;
        text-align: center;
    }
}
</style>

<script>
import PageHeader from '../../components/page-header.vue'
import PageTabbar from '../../components/page-tabbar.vue'
import PageItem from '../../components/page-item.vue'
import Loading from '../../components/loadingImg.vue'
import listJson from '../../config/test-data/item-data.json'
import typeJson from '../../config/test-data/type-data.json'

export default {
    data() {
        return {
            scrollTop: 0, // 滚动距离记录
            list: [],   // 初始化列表
            isLoadingShow: true, 
            jsonData: listJson, // 获取到 模拟数据 列表
            typeData: typeJson, // 模拟数据 类型
            dataIndex: 0,    // 加载的 数据起始位置
            isPromptShow: false,
            isLoadingLiShow: true
        }
    },
    created () {
        // 模拟数据请求
        setTimeout(()=>{
            this.isLoadingShow = false;
            this.isLoadingLiShow = false;
        }, 1500);
        // 获取到第一页数据，10条
        this.list = Array.concat(this.list, this.jsonData.slice(this.dataIndex, this.dataIndex + 10));
        this.dataIndex = this.dataIndex + 10;
    },
    components: {
        PageHeader,
        PageTabbar,
        PageItem,
        Loading
    },
    methods: {
        scrollUl () {
            let Ul = this.$refs.scrollUl;
            let scrollTop = Ul.scrollTop,
                scrollHeight = Ul.scrollHeight,
                ulHeight = Ul.clientHeight;
            if ( scrollHeight - ulHeight - 50 < scrollTop  && this.scrollTop < Ul.scrollTop) {
                if ( !this.isLoadingLiShow ) {  // 禁止在加载的时候重复调用loadData()
                    if ( this.list.length === this.jsonData.length) {
                        this.isPromptShow = true;
                    } else {
                        this.loadData();
                        this.isLoadingLiShow = true;
                    }
                }
            }
            this.scrollTop = scrollTop;
        },
        loadData () {
            setTimeout(() => {
                this.isLoadingLiShow = false;
                this.list = Array.concat(this.list, this.jsonData.slice(this.dataIndex, this.dataIndex + 10));  // 追加数据
                this.dataIndex = this.dataIndex + 10;  // 设置起始位置
            }, 800);
        }
    }
}
</script>


