<template>
  <div id="app">
    <transition :name="transitionName">
      <router-view/>
    </transition>
  </div>
</template>

<script>
  // import css from './config/global-style/style.less'
  import attachFastClick  from './config/plugin/fastclick.js';
  export default {
    name: 'app',
    data () {
      return {
        transitionName: 'slid-left'
      }
    },
    mounted () {
      // attachFastClick(document.body);
    },
    // 过度效果。。。
    watch: {
      '$route'(to, from) {
        const toDepth = to.path.split('/').length
        const fromDepth = from.path.split('/').length
        this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
      }
    }
  }
</script>

<style lang="less">
@import url('./config/global-style/style.less');
#app {
  height: 100%;
}
</style>



